@charset "UTF-8";

/* --------------------------------
 * base
 * -------------------------------- */

body {
  color: #444;
  font-family: "Noto Sans JP", sans-serif;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
a:link,
a:visited,
a:hover,
a:active {
  text-decoration: none;
}
li a {
  color: #444;
}
img {
  display: block;
  width: 100%;
}
p {
  font-size: 16px;
  line-height: 2;
}
.brPc {
  display: none;
}
.underLine {
  background: linear-gradient(transparent 65%, #99e5a5 0%);
}
.color {
  color: #21a937;
}
.tell a {
  color: #21a937;
  font-weight: bold;
}
.tell a::before {
  font-family: "Font Awesome 5 Free";
  content: "\f879";
  font-weight: 900;
  margin-right: 5px;
}
.secTl {
  font-size: 24px;
  font-weight: bold;
}
.boxTl {
  font-size: 24px;
  font-weight: bold;
  line-height: 1.5;
}

.navbar {
  display: flex;
  justify-content: space-between;
  width: calc(100% - 20px);
  margin: 0 auto;
  padding: 10px 0;

  .logo {
    background: url('./images/logo.jpg') no-repeat center center/contain;
    text-indent: 100%;
    white-space: nowrap;
    overflow: hidden;
    width: 50%;
    max-width: 263px;
    height: 73px;
  }
  .infoWrap {
    text-align: right;
    width: 50%;
  }
  .hour {
    font-size: 14px;
    line-height: 1.5;
    margin-bottom: 8px;
  }
  .tell {
    font-size: 20px;
  }
}

.hero {
  background: url('./images/hero.jpg') no-repeat center center/cover;
  height: 500px;

  .container {
    display: flex;
    align-items: center;
    height: 100%;
  }

  .heroTlWrap {
    font-size: 20px;
    font-weight: bold;
    margin-left: 10%;
    writing-mode: vertical-rl;
  }
  .heroTl01,
  .heroTl02 {
    display: block;
    margin-right: 20px;
    padding: 10px 18px;
    background: #fff;
  }
}

.about {
  padding: 40px 0;

  .boxWrap {
    max-width: 600px;
    margin: 0 auto;
  }

  .container {
    width: calc(100% - 30px);
    margin: 0 auto;
  }
  .box {
    margin-top: 30px;
  }
  .boxTl {
    line-height: 2;
    margin-bottom: 37px;
  }
  img {
    border-radius: 10px;
  }
}
.medical {
  background: #f5fcf8;
  padding: 40px 0 60px;

  .container {
    width: calc(100% - 30px);
    margin: 0 auto;
  }
  .secTl {
    text-align: center;
    margin-bottom: 35px;
  }
  .secTxt {
    text-align: center;
    font-weight: bold;
    line-height: 2;
    width: 80%;
    margin: 0 auto 33px;
  }
  .boxWrap {
    max-width: 600px;
    margin: 0 auto;
    margin-bottom: 82px;
  }
  .box {
    background: #fff;
    border: 2px solid #c9c9c9;
    border-radius: 10px;
    margin-top: 20px;
    padding: 40px 30px 0;
  }
  .boxTl {
    text-align: center;
    margin-bottom: 45px;
  }
  .boxTxt {
    font-weight: bold;
    line-height: 2.5;
    margin-bottom: 108px;
  }
  .box img {
    width: 187px;
    height: 187px;
    margin: 0 auto;
  }
}
.brsm {
  display: block;
}

.news {
  .secTl {
    text-align: center;
    margin-bottom: 70px;
  }
  .newsList {
    background: #fff;
    border-radius: 10px;
    box-shadow: 0px 3px 6px #00000029;
    max-width: 600px;
    padding: 20px;
    margin: 0 auto;
  }
  .newsItem {
    border-bottom: 2px solid #e7e7e7;
    padding-bottom: 20px;
    margin: 20px 0;
  }
  .date {
    font-weight: bold;
    color: #21a937;
    margin-right: 10px;
  }
  .cate {
    color: #fff;
    text-align: center;
    background: #444;
    font-size: 12px;
    font-weight: bold;
    display: inline-block;
    width: 80px;
    padding: 4px 16px;
  }
  .post {
    font-weight: bold;
    display: block;
    line-height: 1.5;
    margin-top: 20px;
  }
  .more {
    font-weight: bold;
    display: flex;
    align-items: center;
    justify-content: flex-end;

    &::after {
      font-family: "Font Awesome 5 Free";
      content: "\f0da";
      font-weight: 900;
      font-size: 1.5rem;
      margin-left: 5px;
    }
  }
}

.footer {
  padding: 50px 0;

  .boxLeft {
    margin-bottom: 50px;
    iframe {
      width: 100vw;
      height: 300px;
    }
  }

  .boxRight {
    max-width: 600px;
    width: calc(100% - 30px);
    margin: 0 auto;
  }
  .logo {
    max-width: 263px;
    height: 73px;
    margin-bottom: 25px;
  }
  .address {
    margin-bottom: 25px;
  }
  .zip {
    display: block;
    margin-bottom: 10px;
  }
  .tell a {
    font-size: 24px;
  }
  .tell {
    margin-bottom: 28px;
  }
  .copyLight {
    font-size: 11px;
    text-align: center;
  }
}

.footer {
  .ListBoxWrap {
    margin-bottom: 30px;
  }
  .listBoxLeft {
    border-bottom: 1px solid #e7e7e7;
    margin-bottom: 10px;
    padding-left: 10px;
    padding-bottom: 10px;
  }

  .serviceList {
    margin-bottom: 20px;
  }
  .serviceItem {
    padding-left: 10px;
    margin-bottom: 10px;
  }
  .week {
    display: block;
    margin-right: 10px;
    margin-bottom: 10px;
  }
  .time1 {
    margin-right: 10px;
  }
}

@media (min-width: 768px) {
  .brsm {
    display: none;
  }
  .tell {
    font-size: 24px;
  }
  .tell a {
    pointer-events: none;
  }

  .navbar {
    padding: 29px 0;
  }

  .hero {
    height: 700px;

    .heroTlWrap {
      font-size: 28px;
    }
  }
}

@media (min-width: 1180px) {
  .navbar {
    max-width: 1180px;
  }

  .hero {
    height: 947px;

    .heroTl01,
    .heroTl02 {
      margin-right: 80px;
      margin-left: 0;
      padding: 18px 37px;
    }
  }

  .about {
    padding: 162px 0 82px;

    .boxWrap {
      display: flex;
      max-width: 1180px;
      justify-content: space-between;
      align-items: center;
    }

    .txtBox {
      max-width: 415px;
      margin: 0 44px 0;
    }
    img {
      max-width: 690px;
    }
  }

  .brPc {
    display: block;
  }

  .medical {
    padding: 80px 0 112px;

    .secTl {
      margin-bottom: 68px;
    }

    .boxWrap {
      display: flex;
      justify-content: space-between;
      max-width: 1180px;
    }
    .box {
      flex-basis: 380px;
      height: 590px;
      margin-top: 0;
      position: relative;
    }

    img {
      position: absolute;
      left: 50%;
      transform: translate(-50%, 0);
      bottom: 0;
    }
  }

  .news {
    .container {
      max-width: 1180px;
    }

    .newsList {
      border-radius: 30px;
      max-width: 1180px;
      padding: 50px 70px 45px;
    }

    .date {
      margin-right: 28px;
    }
    .cate {
      margin-right: 28px;
    }
    .post {
      display: inline-block;
    }
    .more {
      margin-top: 40px;
    }
  }

  .footer {
    padding: 173px 0;

    .boxLeft {
      margin-right: 36px;
      margin-bottom: 0;

      iframe {
        width: 608px;
        height: 440px;
      }
    }

    .zip {
      display: inline-block;
      margin-right: 16px;
    }

    .copyLight {
      text-align: right;
    }

    .boxWrap {
      display: flex;
      justify-content: space-between;
      align-items: center;
      max-width: 1180px;
      margin: 0 auto;
    }

    .boxRightIn {
      border-top: 2px solid #e7e7e7;
      border-bottom: 2px solid #e7e7e7;
      margin-bottom: 21px;
    }

    .listBoxLeft {
      border-bottom: 0;
      width: 110px;
      padding-top: 10px;
      padding-right: 28px;
      padding-left: 0;
      margin-bottom: 0;
    }
    .listBoxRight {
      border-left: 2px solid #e7e7e7;
      padding-top: 10px;
      padding-left: 28px;
    }

    .ListBoxWrap {
      display: flex;
      margin-top: 20px;
      margin-bottom: 20px;
    }
    .serviceList {
      margin-bottom: 0;
    }
    .serviceItem {
      display: flex;
      align-items: center;
      margin-bottom: 13px;
      padding: 0;
    }
    .week {
      display: block;
      margin-right: 23px;
      margin-bottom: 0;
    }
    .time1 {
      margin-right: 23px;
    }
  }
}

@media (min-width: 1920px) {
  .hero {
    .heroTlWrap {
      margin-left: 211px;
    }
  }
}

